import { Component } from '@angular/core';
import { IonButton, IonCard, ModalController } from '@ionic/angular';
import { AddAccountPage } from '../add-account/add-account.page';
import { AddTransactionPage } from '../add-transaction/add-transaction.page';
import { AccountService } from '../api/account.service';
import { Account } from '../model/account';
import { Transaction } from '../model/transaction';
import { saveAs } from 'file-saver';
import { Accounts } from '../model/accounts';
import { Renderer2 } from '@angular/core';
import { Router } from '@angular/router';
import { BillReceiptComponent } from '../component/bill-receipt/bill-receipt.component';

@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})
export class HomePage {

  public title: string = "";
  public accounts: Accounts = new Accounts();

  constructor(public accountService: AccountService, private modalController: ModalController, private renderer: Renderer2, private router: Router) {
    this.accountService.getAccounts().subscribe(accounts => {
      this.accountService.accounts = accounts;
      this.accountService.updateAllAccountsBalance();
      this.title = 'Accounts';
      this.updateData();
    })
  }

  public async onAddTransactionClick(account: Account) {
    // this.router.navigate(['/add-transaction'])
    const modal = await this.modalController.create({
      component: AddTransactionPage,
      componentProps: {
        account: account
      }
    });

    modal.onWillDismiss().then(result => {
      this.accountService.updateBalance(account);
      this.updateData();
    })
    
    return await modal.present();
  }

  public async onEditTransactionClick(account: Account, transaction: Transaction) {
    const modal = await this.modalController.create({
      component: AddTransactionPage,
      componentProps: {
        account: account,
        transaction: transaction
      }
    });

    modal.onWillDismiss().then(result => {
      this.accountService.updateBalance(account);
      this.updateData();
    })
    
    return await modal.present();
  }

  public async onViewReceiptClick(event: any, account: Account, transaction: Transaction) {
    event?.stopPropagation();
    if (!transaction.challanDetail) {
      return;
    }
    
    const modal = await this.modalController.create({
      component: BillReceiptComponent,
      cssClass: 'fullscreen',
      componentProps: {
        account: account,
        transaction: transaction
      }
    });

    modal.onWillDismiss().then(result => {
      this.accountService.updateBalance(account);
      this.updateData();
    })
    
    return await modal.present();
  }

  public async onAddAccountClick() {
    const modal = await this.modalController.create({
      component: AddAccountPage,
      componentProps: {
      }
    });

    modal.onWillDismiss().then(result => {
      this.accountService.updateAllAccountsBalance();
      this.updateData();
    })
    
    return await modal.present();
  }

  public async onSaveClick() {
    const blob = new Blob([JSON.stringify(this.accountService.accounts)], {type : 'application/json'});
    saveAs(blob, this.title + '.json');
  }

  public onFileChanged(event: any) {
    let selectedFile = event.target.files[0];
    
    // File name without extension
    this.title = selectedFile.name.replace(/\.[^/.]+$/, "");
    
    if (selectedFile) {
      var reader = new FileReader();
      reader.readAsText(selectedFile, "UTF-8");
      reader.onload = (evt) => {
        this.accountService.accounts = JSON.parse(evt.target.result as string);
        this.accountService.updateAllAccountsBalance();
        this.updateData();
      }
      reader.onerror = function (evt) {
        console.log("error reading file");
      }
    }
  }

  public onViewTransactionsBtnClick(button: IonButton, card: IonCard) {
    if (!button.color || button.color === 'primary') {
      button.color = 'medium';
      this.renderer.removeClass((card as any).el,"no-print");
    } else {
      button.color = 'primary';
      this.renderer.addClass((card as any).el,"no-print");
    }
  }

  public onSearch(event: any) {
    let searchKeyword: string = event.detail.value;
    if (searchKeyword && searchKeyword.trim().length > 0) {
      for (let account of this.accounts.accounts) {
        account.transactions = this.accountService.accounts.accounts
        .find(element => element.id === account.id).transactions
        .filter(transaction => transaction.description.toLowerCase().includes(searchKeyword.toLowerCase()) || transaction.amount.toString().includes(searchKeyword));
      }
    } else {      
      this.updateData();
    }
  }

  public updateData() {
    this.accounts = JSON.parse(JSON.stringify(this.accountService.accounts));
  }

  public identifyAccount(index, item: Account){
    return item.id; 
  }

}
